/* Header ---------------*/
header #logo,
header nav {
	text-align: center;
}

/* Navigation ---------------*/
nav#main-nav ul {
	padding: 0;
	margin: 0;
}
nav#main-nav ul li {
	list-style: none;
	margin-bottom: 5px;
}

nav a {
	text-decoration: none;
	text-transform: uppercase;
	font-weight: 700;
	color: #4a4a4a;
	padding: 10px 15px;
	background-color: #fff;
	display: block; 
}

nav a:hover {
	background-color: #252525;
	color: #fff;
}

/* Media Queries ---------*/
@media screen and (min-width: 768px) {
	/* Navigation ---------------*/
	nav#main-nav ul li {
		display: inline-block;
	}

	nav a {
		background-color: transparent;
		padding: 10px 25px;
	}
}

@media screen and (min-width: 1024px) {
	/* Navigation ---------------*/
	#logo {
		float: left;
	}
	nav#main-nav {
		float: right;
	}

	nav#main-nav ul li {
		display: inline-block;
	}

	nav a {
		padding: 10px 15px;
	}
}